'use client'

import { Crown, Skull, Flame, Star, Diamond, Rocket, Bot, Swords, Ship, Alien } from 'lucide-react'

interface AvatarProps {
  icon: string
  size?: 'sm' | 'md' | 'lg' | 'xl'
  className?: string
}

export function Avatar({ icon, size = 'md', className = '' }: AvatarProps) {
  const icons: Record<string, any> = {
    crown: Crown,
    skull: Skull,
    fire: Flame,
    star: Star,
    diamond: Diamond,
    rocket: Rocket,
    robot: Bot,
    ninja: Swords,
    pirate: Ship,
    alien: Alien
  }

  const sizes = {
    sm: { container: 'w-8 h-8', icon: 'w-4 h-4' },
    md: { container: 'w-12 h-12', icon: 'w-6 h-6' },
    lg: { container: 'w-16 h-16', icon: 'w-8 h-8' },
    xl: { container: 'w-24 h-24', icon: 'w-12 h-12' }
  }

  const Icon = icons[icon] || Crown

  return (
    <div className={`${sizes[size].container} rounded-full bg-gradient-to-br from-[#5cb85c] to-[#4a9d4a] flex items-center justify-center border-2 border-[#5cb85c] shadow-lg ${className}`}>
      <Icon className={`${sizes[size].icon} text-white`} />
    </div>
  )
}